.item {
    display: flex;
    justify-content: space-between;
    margin-bottom: 70px;
}

.posts {
    margin-right: 20px;
}

.text {
    position: relative;
    margin-left: 60px;
}

.text:before {
    content: '';
    display: block;
    position: absolute;
    top: -20px;
    left: -80px;
    border-radius: 50%;
    background-color: #6ad16e;
    padding: 30px;
}

.likes {
    margin-right: 10px;
}

.btnLike, .btnComment {
    border:none;
    background: none;
    cursor: pointer;
    outline: none;
}

.btnLike {
    color: red;
    font-size: 18px;
    margin: 5px;
}

.btnComment {
    color: lightblue;
    font-size: 18px;
}