.usersContainer {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 15px;
}

.ava {
    width: 200px;
    height: 200px;
    border-radius: 5px;
}
.name {
    margin:3px 0;
    text-align: center;
}

.btn {
    border: none;
    width: 100%;
    color: white;
    padding: 10px;
    border-radius: 10px;
    margin-right: 8px;
}
.btnFoll {
    background-color: #6ad16e;
}
.btnUfoll {
    background-color: red;
}
.usersList {
    margin-bottom: 13px;
    margin-right: 6px;
}
