.infoImage img {
    max-height: 250px;
    border: 1px solid gray;
    padding: 5px;
    box-shadow: 0 0 5px;
}
.infoImage {
    max-width: 280px;
}

.info {
    display: flex;
}

.profileInfo {
    margin-left: 200px;
    margin-top: 20px;
}

.profileHeader {
    margin-bottom: 5px;
}
.workT, .workF {
    margin: 15px auto;
    margin-top: 20px;
    min-width: 120px;
    max-height: 35px;
    padding: 8px 28px 8px;
    border-radius: 10px;
    text-align: center;

}

.workT {
    background-color: #6ad16e;

}
.workF {
    background-color: #ffd363;
}
.wrapper_description {
    text-align: center;
    margin-top: 5px;
}
.description {
    font-weight: normal;
}
.links {

    margin-top: 30px;

}
.items {
    display: block;
    margin-left: 15px;
    font-size: 20px;
}
.twit {
    color: #39ccff;
}
.git {
    color: black;
}
.inst {
    color: #ff5c7a;
}
.editProfile {
    position: absolute;
    top: 0;
    right: 10px;
    opacity: 0;
    color: #dcdcdc;
    border: none;
    background: inherit;
    transition: opacity .2s ease .1s;
}
.wrapper {
    position: relative;
    min-width: 250px;
}
.wrapper:hover .editProfile {
    opacity: 1;
}
.about {
    margin-top: 20px;
    margin-bottom: 10px;
}