.header {
    background-color: #6ad16e;
    grid-area: header;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo{
    padding-left: 40px;
    font-size: 30px;
    color: white;
    text-shadow: -3px 5px 4px gray,
                 -6px 7px 2px gainsboro ;
}

.login__block {
    margin-right: 20px;
}

.login__block a , .login {
    color: white;
    font-size: 18px;
}
.logout {
    cursor: pointer;
    color: #ffd363;
}
