*,
*:before,
*:after {
  margin: 0 ;
  padding: 0;
    outline: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
a, button {
    cursor: pointer;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}

.app__wrapper {
    display: grid;
    grid-template-areas:
    "header header "
    "nav cont ";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    width: 1050px;
    margin: 0 auto;
}

.app__wrapper__content {
    grid-area: cont;
}




