.name {
    display: flex;
    font-size: 16px;
    color: white;
    margin-right: 20px;
    margin-top: 40px;
    position: relative;
}

.name:before {
    content: '';
    display: block;
    padding: 15px;
    background-color: gold;
    border-radius: 50%;
    position: absolute;
    top: -33px;
    left: 50%;
    transform: translateX(-50%);
}