.login {
    margin: 50px auto;
    max-width: 300px;
    min-height: 250px;
    background-color: #6ad16e;
    border-radius: 10px;
}
.login__wrapper {
    padding: 50px 20px;
}
.header {
    text-align: center;
    color: white;
}
.inputs__item {
    width: 100%;
    margin-top: 10px;
    padding: 6px;
    border-radius: 10px;
    border: none;
}
.inputs__item{
    display: flex;
    justify-content: center;
}
.inputs__dlc {
    margin-top: 10px;
}
.check__text {
    display: inline-block;
    margin-left: 5px;
    color: white;
}
.btn__block{
    margin-top: 10px;
}
.btn__block button{
    width: 100%;
    border: none;
    background-color: #2cb8d1;
    color: white;
    border-radius: 10px;
    padding: 7px;
}
.formError {
    border:1px solid red;
    border-radius: 10px;
    padding: 4px;
    color: #ff3628;
    text-align: center;
}