.dialogsItem {
    margin-top: 30px;
    margin-bottom: 40px;
    padding-right: 60px;
    padding-left: 80px;
    position: relative;
}

.dialogsItem a:before {
    content: '';
    display: block;
    position: absolute;
    top: -10px;
    left: 20px;
    padding: 20px;
    border-radius: 50%;
    background-color: #6ad16e;
}