.menu {
    background-color: #6ad16e;
    grid-area: nav;
    max-height: 500px;
    padding: 20px;
}

.item {
    padding-top: 3px;
}

.text  {
    color: #fff;
    font-size: 20px;
}

.text.active {
    color: gold;
}

.item:last-child {
    margin-top: 20px;
}

.textMod {
    font-size: 35px;
}

.friendsList {
    display: flex;
    margin-left: 20px;
    flex-wrap: wrap;
}