.paginator {
    display: flex;
    justify-content: center;
    align-items: center;
}
.btn {
    background: inherit;
    border: 1px solid #6ad16e;
    border-radius: 7px;
    padding: 5px;
    margin-right: 5px;
}
.btn:nth-child(2) {
    margin-right: 0;
}
.selectedPage {
    font-weight: bold;
}
span {
    cursor: pointer;
    margin: 0 5px;
}
