.dialogs {
    display: flex;
    justify-content: space-around;
}

.messages {
    position: relative;
}
.messageList {
    height: 500px;
    width: 642px;
    overflow: hidden;
    overflow-y:scroll;
    box-shadow: 1px 1px 5px 1px #6ad16e;
    border-radius: 5px;

}
.text input{
    position: absolute;
    bottom: -41px;
    border: none;
    box-shadow: 1px 1px 5px 1px #6ad16e;
    border-radius: 5px;
    left: 0;
    width: 100%;
    min-height: 40px;
    padding-left: 20px;
}

::-webkit-scrollbar {
    width: 0px;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}

.btnMessage {
    position: absolute;
    bottom: -41px;
    right: 0;
    height: 40px;
    width: 100px;
    background-color: inherit;
    border: 1px solid #6ad16e;
    border-radius: 5px;

}
